___CSS_LOADER_EXPORT___.push([module.id, `*{
  margin: 0px;
  padding: 0px;
}

p, h1, h2, h3, h4, a{
  font-family: 'Poppins', sans-serif;
}

.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  background-color: #bf1722;
  color: white;
  padding: 8px;
  z-index: 100;
}
 
.skip-link:focus {
   top: 0;
}

body, html{
  min-width: fit-content;
  display: grid;
  background-color: #3C3C3C;
}

ul a{
  min-width: 44px;
  min-height: 44px;
}

.restaurant-container{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
  justify-items: center;
  gap: 1rem;
  margin: 1rem;
}

.catalog{
  display: flex;
  flex-flow: column;

  background-color: whitesmoke;
  width: 100%;
  height: 500px;
}

.search{
  margin: 1rem;
  display: flex;
  flex-flow: column;
  align-items: center;
  gap: 1rem;
}
.search-input{
  font-family: 'Poppins', sans-serif;
  font-size: 1rem;
  text-align: center;
  padding: 0.5rem;
  height: 44px;
  width: 75%;
  border-radius: 5rem;
  border: none;
  background-color: whitesmoke;
}
.search-submit{
  font-family: 'Poppins', sans-serif;
  font-size: 0.85rem;
  text-align: center;
  padding: 0.25rem;
  min-height: 44px;
  width: 125px;
  border-radius: 5rem;
  border: none;
  font-weight: bold;
  background-color: #c9c9c9;
}

.like{
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;
  
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  
  font-size: 18px;
  color: white;
  cursor: pointer;
}
.review-tampil{
  display: flex;
  justify-content: center;
  margin: 1rem 0rem 1.5rem 0rem;
}
.addreview{
  display: none;
  flex-flow: column;
  gap: 1rem;
  margin: 1.5rem;
  align-items: center;
}
.addreview.active{
  display: flex;
}
.addreview-name, .addreview-review{
  width: 500px;
  padding: 1rem;
  font-family: 'Poppins', sans-serif;
  font-size: 1rem;
  border: none;
  background-color: whitesmoke;
}
.addreview-name{
  border-radius: 1.5rem;
}
.addreview-review{
  height: 350px;
  border-radius: 1rem;
}
.addreview-submit, .review-tampil-button{
  width: 150px;
  height: 44px;
  font-family: 'Poppins', sans-serif;
  font-weight: bold;
  border-radius: 5rem;
  border: none;
  background-color: #c9c9c9;
  cursor: pointer;
}
.addreview-submit:hover, .review-tampil-button:hover{
  background-color: black;
  color: whitesmoke;
}`, "",{"version":3,"sources":["webpack://./src/styles/main.css"],"names":[],"mappings":"AAAA;EACE,WAAW;EACX,YAAY;AACd;;AAEA;EACE,kCAAkC;AACpC;;AAEA;EACE,kBAAkB;EAClB,UAAU;EACV,OAAO;EACP,yBAAyB;EACzB,YAAY;EACZ,YAAY;EACZ,YAAY;AACd;;AAEA;GACG,MAAM;AACT;;AAEA;EACE,sBAAsB;EACtB,aAAa;EACb,yBAAyB;AAC3B;;AAEA;EACE,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,4DAA4D;EAC5D,qBAAqB;EACrB,SAAS;EACT,YAAY;AACd;;AAEA;EACE,aAAa;EACb,iBAAiB;;EAEjB,4BAA4B;EAC5B,WAAW;EACX,aAAa;AACf;;AAEA;EACE,YAAY;EACZ,aAAa;EACb,iBAAiB;EACjB,mBAAmB;EACnB,SAAS;AACX;AACA;EACE,kCAAkC;EAClC,eAAe;EACf,kBAAkB;EAClB,eAAe;EACf,YAAY;EACZ,UAAU;EACV,mBAAmB;EACnB,YAAY;EACZ,4BAA4B;AAC9B;AACA;EACE,kCAAkC;EAClC,kBAAkB;EAClB,kBAAkB;EAClB,gBAAgB;EAChB,gBAAgB;EAChB,YAAY;EACZ,mBAAmB;EACnB,YAAY;EACZ,iBAAiB;EACjB,yBAAyB;AAC3B;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,yBAAyB;;EAEzB,eAAe;EACf,YAAY;EACZ,WAAW;EACX,kBAAkB;EAClB,SAAS;;EAET,eAAe;EACf,YAAY;EACZ,eAAe;AACjB;AACA;EACE,aAAa;EACb,uBAAuB;EACvB,6BAA6B;AAC/B;AACA;EACE,aAAa;EACb,iBAAiB;EACjB,SAAS;EACT,cAAc;EACd,mBAAmB;AACrB;AACA;EACE,aAAa;AACf;AACA;EACE,YAAY;EACZ,aAAa;EACb,kCAAkC;EAClC,eAAe;EACf,YAAY;EACZ,4BAA4B;AAC9B;AACA;EACE,qBAAqB;AACvB;AACA;EACE,aAAa;EACb,mBAAmB;AACrB;AACA;EACE,YAAY;EACZ,YAAY;EACZ,kCAAkC;EAClC,iBAAiB;EACjB,mBAAmB;EACnB,YAAY;EACZ,yBAAyB;EACzB,eAAe;AACjB;AACA;EACE,uBAAuB;EACvB,iBAAiB;AACnB","sourcesContent":["*{\n  margin: 0px;\n  padding: 0px;\n}\n\np, h1, h2, h3, h4, a{\n  font-family: 'Poppins', sans-serif;\n}\n\n.skip-link {\n  position: absolute;\n  top: -40px;\n  left: 0;\n  background-color: #bf1722;\n  color: white;\n  padding: 8px;\n  z-index: 100;\n}\n \n.skip-link:focus {\n   top: 0;\n}\n\nbody, html{\n  min-width: fit-content;\n  display: grid;\n  background-color: #3C3C3C;\n}\n\nul a{\n  min-width: 44px;\n  min-height: 44px;\n}\n\n.restaurant-container{\n  display: grid;\n  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));\n  justify-items: center;\n  gap: 1rem;\n  margin: 1rem;\n}\n\n.catalog{\n  displa